@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);
html{
  height:100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #f7f7f7;
}

.cards{
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-size:14px;
  width:100%;
  margin-top:80px;
  text-align:center;
  border-radius:10px;
box-shadow: 0px 0px 23px 8px rgba(234,234,234,1);
}
.cards .start{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color:white;
  background-color:#1ae396;
  padding:20px;
}
.cards .start h1{
  font-size:40px;
  font-weight:900;
}
.cards .start h4{
  font-size:15px;
  text-align:left;
  padding: 0;
}
.cards h5{
  padding:20px;
}
.cards p{
  font-style: italic;
  text-align: right;
  font-size:12px;
  margin-bottom:5px;
  padding-right: 10px;
}
ul{
    margin-top:0;
    margin-bottom:0;
}

.toolbar{
    position:fixed;
    height:60px;
    width:100%;
    top:0;
    left:0;
    background-color:#1ae396;
    z-index: 500;
}

.toolbar_navigation{
    display:flex;
    height:100%;
    align-items:center;
    padding:0 1rem;
}

.toolbar_logo{
    margin-left:0.5rem;
}

.toolbar_logo a{
    color:white;
    text-decoration: none;
    font-size:1.5rem;
}

.spacer{
    flex:1 1;
}

.toolbar_navigation_items ul{
    list-style: none;
    margin:0;
    padding:0;
    display:flex;
}

.toolbar_navigation_items li {
    padding: 0 0.5rem;
}

.toolbar_navigation_items a {
    color:white;
    text-decoration: none;
}

.toolbar_navigation_items a:hover,
.toolbar_navigation_items a:active {
    color:#06422b;
}

@media (max-width:768px){
    .toolbar_navigation_items{
        display:none;
    }
}
.hamburger{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    height:24px;
    width:30px;
    background:transparent;
    border:none;
    cursor: pointer;
    padding:0;
    box-sizing: border-box;
}

.hamburger:focus{
    outline: none;
}

.hamburger-piece{
    width:30px;
    height:2px;
    background: white;
}


.side_drawer{
    height: 100%;
    background:white;
    box-shadow:1px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top:0;
    left:0;
    max-width:400px;
    width:70%;
    z-index:200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side_drawer.open{
    transform: translateX(0);
}

.side_drawer ul{
    height:100%;
    list-style:none;
    display:flex;
    flex-direction: column;
    justify-content: center;
    background: rgb(26,227,150);
background: linear-gradient(360deg, rgba(26,227,150,1) 48%, rgba(18,136,91,1) 100%);
}

.side_drawer li{
    margin: 0.5rem 0;
    color:white;
    font-weight:bold;
}

.side_drawer a{
    color:indianred;
    text-decoration:none;
    font-size:1.2rem;
}


.side-drawer a:hover,
.side_drawer a:active{
    color:#fa923f;
}

@media (min-width:796px){
    .side_drawer{
        display:none;
    }
    .hamburger{
        display: none;
    }
}
#loader {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  width: 100px;
  height: 100px;
  margin: -75px 0 0 -75px;
  border: 6px solid #f3f3f3;
  border-radius: 50%;
  border-top: 6px solid #3498db;
  width: 70px;
  height: 70px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Add animation to "page content" */
.animate-bottom {
  position: relative;
  -webkit-animation-name: animatebottom;
  -webkit-animation-duration: 1s;
  animation-name: animatebottom;
  animation-duration: 1s
}

@-webkit-keyframes animatebottom {
  from { bottom:-100px; opacity:0 } 
  to { bottom:0px; opacity:1 }
}

@keyframes animatebottom { 
  from{ bottom:-100px; opacity:0 } 
  to{ bottom:0; opacity:1 }
}



